import getService from "../../services";

export function loadMatches(id) {
  debugger;
  return dispatch => {
    return getService(`http://localhost:5000/api/matches/${id}`, "GET")
      .then(response => {
        dispatch({ type: "LOAD_MATCHES", matches: response.data });
      })
      .catch(e => console.log(e));
  };
}

export function loadDeliveries(id) {
  return dispatch => {
    return getService(`http://localhost:5000/api/deliveries/${id}`, "GET")
      .then(response => {
        dispatch({ type: "LOAD_DELIVERIES", deliveries: response.data });
      })
      .catch(e => console.log(e));
  };
}

export function pageInc() {
  return dispatch => {
    return dispatch({ type: "PAGE_INC" });
  };
}
export function pageDec() {
  return dispatch => {
    return dispatch({ type: "PAGE_DEC" });
  };
}
